/* Profile Page Styles */

.profile-container {
  font-family: 'Montserrat', sans-serif !important;

}

.profile-card {
  background: white;
  padding: 2rem;
  border-radius: 12px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  text-align: center;
  width: 80%;
  margin-top: 20px;
  margin-left: 10%;
  margin-right: 10%;
  min-height: 450px;
}

.profile-avatar {
  margin-bottom: 1rem;
  display: flex;
  justify-content: center;
}

.avatar-img {
  width: 100px;
  height: 100px;
  object-fit: cover;
  border-radius: 50%;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
}

.edit-btn,
.delete-btn,
.save-btn,
.close-btn {
  display: inline-block;
  margin-top: 1rem;
  padding: 10px 15px;
  border-radius: 8px;
  font-weight: bold;
  cursor: pointer;
  transition: 0.3s ease-in-out;
}

.edit-btn {
  width: 120px;
  padding: 5px;
  border-width: 1px;
  border-radius: 18px;
  margin-bottom: 20px;
  font-size: 12px;
  border-style: solid;
  border-color: #4F46E5;
  color: #4F46E5;
  background-color: transparent;
  margin-left: 4px;
  margin-right: 4px;
}

.edit-btn:hover {
  background-color: #4F46E5;
  color: white;
}

.delete-btn {
  width: 120px;
  padding: 5px;
  border-width: 1px;
  border-radius: 18px;
  margin-bottom: 20px;
  font-size: 12px;
  border-style: solid;
  border-color: #dc3545;
  color: #dc3545;
  background-color: transparent;
  margin-left: 4px;
  margin-right: 4px;
}

.delete-btn:hover {
  background-color: #b52b3a;
  color: white;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  background: white;
  padding: 2rem;
  border-radius: 12px;
  width: 300px;
  text-align: center;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}

.upload-label {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  padding: 10px;
  border: 2px dashed #007bff;
  border-radius: 8px;
  cursor: pointer;
  transition: 0.3s;
}

.upload-label:hover {
  background: #f0f4f8;
}

.input-field {
  width: 100%;
  padding: 8px;
  margin-top: 5px;
  border: 1px solid #ccc;
  border-radius: 6px;
}

.preview-img {
  width: 80px;
  height: 80px;
  object-fit: cover;
  border-radius: 50%;
  margin-top: 10px;
}

.close-btn {
  background: #6c757d;
  color: white;
  border: none;
}

.close-btn:hover {
  background: #495057;
}

.account-details-user {
  background-color: rgb(246, 246, 246);
  min-height: 250px;
  width: 98%;
  margin-left: auto;
  margin-right: auto;
  border-radius: 25px;
  margin-top: 16px;
  margin-bottom: 6px;
  padding-top: 20px;
}

.account-details-user span {
  position: relative;
}

.account-details-user svg {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: -15px;
}

.account-details-user h2 {
  font-family: vice;
  font-weight: bold;
  margin-bottom: 20px;
}